import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion'; // Import motion from framer-motion
import './Item.css';

const Item = (props) => {
  const { id, image, image2, title, old_price, new_price } = props;
  const [isHovered, setIsHovered] = useState(false);
  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };
    return (
    <motion.div 
      className='Parent-item flex flex-col bg-white ' // Removed fixed width, use flex for responsiveness
    > 
 <div className='img-parent w-full flex justify-center'>
        <Link to={`/product/${id}`}>
          <motion.img
            src={isHovered ? image2 : image}
            className='item-image'
            alt=""
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
          />
        </Link>
      </div>
      <div className='title-container mt-[4px] '>
        <Link to={`/product/${id}`}>
          <p className='title  opacity-95 md:text-[22px] sm:text-[18px] xsm:text-[16px] '>{title}</p>
        </Link>
      </div>
      <div className="item-prices flex gap-4 mt-[4px] px-[1px] items-center">
      <div className="item-newPrice xsm:text-[14px] sm:text-[18px] md:text-[22px] font-b">Rs.{new_price}</div>
        <div className="item-oldPrice xsm:text-[14px] opacity-60 sm:text-[18px] line-through md:text-[22px]">Rs.{old_price}</div>
  </div>
     
    </motion.div>
  );
};

export default Item;
