// ShopContextProvider.js
import React, { createContext, useState } from 'react';
import Datas from "../Components/Data/AllDatas";

export const ShopContext = createContext(null);

const getDefaultCart = () => {
    let cart = {};
    Datas.forEach(item => {
        cart[item.id] = {};
    });
    return cart;
};

const ShopContextProvider = (props) => {
    const [cartItems, setCartItems] = useState(getDefaultCart());
    const [searchQuery, setSearchQuery] = useState('');

    const addToCart = (itemId, size) => {
        setCartItems(prev => {
            const updatedCart = { ...prev };
            const item = updatedCart[itemId];
            const quantity = item[size] ? item[size] + 1 : 1;
            return { ...prev, [itemId]: { ...item, [size]: quantity } };
        });
    };

    const removeFromCart = (itemId, size) => {
        setCartItems(prev => {
            const updatedCart = { ...prev };
            const item = updatedCart[itemId];
            if (item && item[size]) {
                const quantity = item[size] - 1;
                if (quantity === 0) {
                    delete item[size];
                } else {
                    item[size] = quantity;
                }
            }
            return { ...prev, [itemId]: { ...item } };
        });
    };
    const getTotalItemCount = () => {
        let totalCount = 0;
        Object.values(cartItems).forEach(item => {
            Object.values(item).forEach(quantity => {
                totalCount += quantity;
            });
        });
        return totalCount;
    };


    const updateSearchQuery = (query) => {
        setSearchQuery(query);
    };

    const contextValue = { 
        Datas, 
        cartItems, 
        addToCart, 
        removeFromCart, 
        getTotalItemCount, 
        searchQuery,
        updateSearchQuery 
    };

    return (
        <ShopContext.Provider value={contextValue}>
            {props.children}
        </ShopContext.Provider>
    );
};

export default ShopContextProvider;
