import React from "react";
import RightArrow from "../images/right-arrow.png";
const BreadCrum = (props) => {
  const { product } = props;
  return (
    <div className="Parent-breadCrum px-8 xsm:pt-20 md:pt-28 flex items-center gap-1 md:text-[14px]">
      <p className="xsm:text-[12px]">Home </p>
      <img src={RightArrow} className="w-3 h-3 " alt="" />
      <p className="xsm:text-[12px]">{product.category} </p>
      <img src={RightArrow} className="w-3 h-3" alt="" />
      <p className="xsm:text-[12px]" style={{ fontSize: "10px" }}>
        {product.title.length > 30
          ? `${product.title.slice(0, 30)}...`
          : product.title}
      </p>
    </div>
  );
};

export default BreadCrum;
