import React from "react";
import "./Category.css";
import Shirts from "../../images/Shirts.jpg";
import Bottoms from "../../images/bottoms.jpg";
import Tshirts from "../../images/Tshirts.jpg";
import Shoes from "../../images/Shoes.jpg";
import Hoodies from "../../images/Hoodies.jpg";
import Eyewear from "../../images/Eyewear.jpg";
import { Link } from "react-router-dom";

const Category = ({type}) => {
  const categories = [
    { name: "Shirts", image: Shirts,type:"Shirt"},
    { name: "Bottoms", image: Bottoms,type:"Bottom" },
    { name: "TShirts", image: Tshirts,type:"TShirt" },
    { name: "Shoes", image: Shoes,type:"Shoe" },
    { name: "Hoodies", image: Hoodies ,type:"Hoodie"},
    { name: "Eyewear", image: Eyewear ,type:"Eyewear" },
  ];
  return (
    <section className=" section-category pt-32 ">
      <h1 className=" text-center">Shop by Category</h1>
      <div className="Parent-category pt-20 xsm:px-6 md:px-12 lg:px-12 xl:px-0 pb-20 grid xsm:grid-cols-1 md:grid-cols-2  lg:grid-cols-3 xl:grid-cols-6">
        {categories.map((category, i) => (
          <div
            key={i}
            className="category-item flex flex-col relative items-center justify-center ci1"
          ><Link to = {`/${category.type}`}>
            <img
              src={category.image}
              alt=""
              className="w-full absolute left-0 top-0 "
              srcset=""
            />
            </Link>
            <h2 className="absolute bottom-0">{category.name}</h2>
          </div>
        ))}
      </div>
    </section>
  );
};

export default Category;
